const Transition = posed.div({
  preEnter: {
    x: 1000,
    opacity: 1,
  },
  enter: {
    position: 'fixed',
    opacity: 1,
    y: 0,
    x: 0,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
    exit: { 
      x: 1000,
      opacity: 1,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },    
  },
})



